#initial-form{
    text-align: center;
    margin: 5vh 0 10vh 0;
}
h3{
    color: whitesmoke;
    text-transform: uppercase;
    font-family: 'Audiowide', cursive;
    text-align: center;
    font-size: 3em;
    margin-top: 1vw;
    position: relative;
    padding: 0, 0, 0, 0;
}
.basic-info-table{
    height: 10vh;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-bottom: 33vh;
}
.basic-info {
    text-align: center;
    margin-bottom: 5%;
}
.basic-info div span {
    font-weight: 700;
    color: deeppink;
}
.basic-info div{
    font-size: 1em;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.7);
    border: 1px solid aqua;
    padding: 2%;
}
.basic-desc>div{
    font-size: 1em;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.7);
    border: 1px solid deeppink;

    height: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 2.7%;
}

input,button{
    border: 2px solid rgba(255,255,255,0.1);
    background-color: rgba(0, 0, 0, 0.7);
    border: 1px solid whitesmoke;
    border-radius: 10px;
    text-align: center;
    font-size: 1em;
    outline: none;
    margin: 1em;
    padding: 1em;
    width: 200px;
    margin-bottom: 0;
}

button{
    padding: 0.4em;
    -webkit-transition-duration: 0.5s;
         -o-transition-duration: 0.5s;
            transition-duration: 0.5s;
    text-align: center;
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 0;
}
button[type="submit"]{
        color: black;
        background-color: chartreuse;
        border-color: black;
    }
button[type="reset"]{
        color: black;
        border-color: black;
        background-color: deeppink;
    }
.secondary-info-table {
    padding: 5%;
    margin-bottom: 1vh;
    padding-bottom: 0;
}

.secondary-info {
    background-color: rgba(0, 0, 0, 0.7);
    border: 1px solid chartreuse;
    padding: 5%;
    margin-top: 1vh;
    border-radius: 10px;
    margin-bottom: 5vh;
}
.secondary-info.implants{
    margin-bottom: 20vh;
}

.secondary-info p {
    text-align: center;
    font-size: 130%;
}
.stuff p{
    font-size: 0.8em;
    text-align: left;
}
.basic-desc p{
   margin: 0.3em; 
}

/*            RWD             */



@media (min-width: 575px){
    .basic-info-table{
        margin-bottom: 100vw;
    }
}
@media (min-width: 768px){
    h3{
        text-align: center;
        font-size: 2.5em;
        padding: 2.5vw 0;
    }
    .basic-info div{
        padding: 4.5%;
    }
    .basic-info-table{
        margin-bottom: 40vw;
    }
    input{
        padding: 0.5em 0;
        font-size: 1.2em;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        width: 15em;
    }
    button{
        width: 15em;
        padding: 0.5em;
        font-size: 1.2em;
    }
    .initial-form{
        text-align: right;
    }
    button[type="submit"]:hover{
        color: black;
        background-color: chartreuse;
    }
    button[type="reset"]:hover{
        color: black;
        background-color: deeppink;
    }
}
@media (min-width: 992px){
    h3{
        text-align: left;
        font-size: 1.9em;
        padding: 2.5vw 0;
    }
    input{
        padding: 1em 0.5em;
        font-size: 0.8em;
        width: 10em;
        margin-top: 1em;
    }
    .basic-info-table{
        margin-bottom: 0;
    }
/*                                          */

    button[type="submit"],button[type="reset"]{
        color: black;
        background-color: rgba(245,245,245,0.7);
        width: 6em;
        font-size: 1em;
        padding: 0.8em 0;
        margin-top: 0.7em;
        margin-left: 1em;
    }
}


